                                    </thead>
                                    <tbody>
                                        <tr><td></td><td class="tx-left"></td></tr>
                                        <tr><td></td><td class="tx-left"></td></tr>
                                        <tr><td></td><td class="tx-left">  </td></tr>
                                        <tr><td></td><td class="tx-left"> </td></tr>
                                        <tr><td></td><td class="tx-left"> </td></tr>
                                        <tr><td></td><td class="tx-left">1,000,000</td></tr>
                                        <tr><td></td><td class="tx-left">2021.02.01 ~ 2021.12.31</td></tr>
                                        <tr><td> </td><td class="tx-left">    </td></tr>
                                        <tr><td></td><td class="tx-left">  </td></tr>
                                        <tr><td></td><td class="tx-left">   </td></tr>
                                        <tr><td></td><td class="tx-left"> </td></tr>
                                    </tbody>
                                </table>

                            </div>
                            <div class="col no-change-month" >
                                 <div class="card-header  pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i>  </h6>
                                    <button class="btn btn-info"></button>
                                </div>
                                <vue-calendar
                            :show-limit="3"
                            :events="events"
                            @event-clicked="eventClicked"

                            ></vue-calendar>

                            </div>
                        </div>
                        
                        <div class="row  tx-center">
                            <div class="col">
                                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i>  </h6>
                                    <button class="btn btn-info"></button>
                                </div>
                                <table class="table table-bordered table-mid tx-13 tx-gray-700 bd">
                                    <thead>
                                         <tr class="bg-gray-100 tx-11 tx-uppercase tx-gray-800">
                                             <th class="tx-center"></th>
                                             <th class="tx-center">WIP</th>
                                             <th class="tx-center">Shared</th>
                                             <th class="tx-center">Publish</th>
                                             <th class="tx-center">Archive</th>
