                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td>()</td><td>19</td><td>10</td><td>10</td><td>5</td>
                                        </tr>
                                        <tr>
                                            <td>()</td><td>100</td><td>50</td><td>37</td><td>20</td>
                                        </tr>
                                        <tr>
                                            <td colspan="5" class="text-right">  44 207 </td>
